@import '../../responsive.less';

.options_container {
  width: 100%;
  height: 100%;
}

.options_page {
  width: 100%;
  min-height: 100%;
  .desktop({
    display: flex;
    height: 100%;
    overflow-y: hidden;
  });
  .mobile({
    background: #fcfdff;
    })
}

.options_filler {
  width: 33.3333333333%;
  height: 100%;
}
.options_section {
  position: fixed;
  background: white;
  border-right: 2px solid rgb(220,221,222);
  width: 33.3333333333%;
  height: 100%;
  overflow-y: auto;
}

.actions_section {
  background: rgb(252,253,255);
  width: 66.6666666667%;
  height: 100%;
  padding-bottom: 25em;
  overflow-y: auto;
}
.action_button {
  height: 2.5em;
  width: 100%;
}
.action_button_text {
  color: white;
  font-size: 0.75em;
  font-weight: 600;
  letter-spacing: 0.05em;
}



